import React from "react"
import LevellingTable from "./levellingTable.js"
import DropDown from "../dropDown.js"
import styles from "../../styles/classes/classFeatures.module.css"
import arrow from "../../images/down-arrow.png";

export default () => (
    <div>
        <FeatureDropDown name="Bard Levelling Table"><LevellingTable/></FeatureDropDown>
        <FeatureDropDown name="Class Features"><LevellingTable/></FeatureDropDown>
        <FeatureDropDown name="SpellCasting"><LevellingTable/></FeatureDropDown>
        <FeatureDropDown name="Bardic Inspiration"><LevellingTable/></FeatureDropDown>
        <FeatureDropDown name="Jack of All Trades"><LevellingTable/></FeatureDropDown>
    </div>
)

const FeatureDropDown = (props) => {
    return (
        <DropDown
            header={<FeatureHeader name={props.name}/>}
            arrow={<FeatureArrow/>}
            classname={styles.dropdown}
            childrenStyle={dropdownContent}
        >
            {props.children}
        </DropDown>
    )
}

const FeatureHeader = (props) => {
    return (
        <p className={styles.dropdownText}>{props.name}</p>
    )
}

const FeatureArrow = () => {
    return (
        <img src={arrow} alt="" className={styles.dropdownArrow}/>
    )
}

const dropdownContent = {
    position: "relative",
    width: "296px",
    left: "8px",

    background: "#4C5057",
    borderRadius: "10px",
};

